.slideUp {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  width: 3rem;
  height: 3rem;
  color: white;
  background-color: var(--yellow);
  border-radius: 25%;
  position: fixed;
  bottom: 3%;
  right: 5%;
  z-index: 2;
  cursor: pointer;
}
.slideUp:hover {
  background-color: white;

}
.topbtnIcon {
  color: var(--blue);
}

.slideUp:active {
    transform: translateY(4px);
  }
  @media screen and (max-width: 768px) {
    .slideUp {
      font-size: 1rem;
      width: 2rem;
      height: 2rem;
    }
    .slideUp{
      right: 2rem;
      bottom: 2rem;
    }
  }