

.facts {
  height: 100%;
  overflow: hidden;
  position: relative;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/divine-public-school-e196f.appspot.com/o/websiteImages%2Fadmission%2Fboardkids_11zon.jpg?alt=media&token=4c840099-b266-46a8-ab09-8f8dfb6f7244");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: fill;
  width: 100%;
  backdrop-filter: blur(10px);
}

.facts:before {
  content: "";
  position: absolute;
  z-index: -1;
  background: rgba(21, 10, 107, 0.392);
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
}
.factOverlay {
  height: 100%;
  margin: 10em auto;
  width: 50%;
}

.factData {
  padding: 3rem 5rem;
  background-color: rgb(208, 171, 37);
}
.factData h2 {
  font-size: var(--head2);
  letter-spacing: 5px;
  font-family: var(--font-family-1);
}
.factData h1 {
  font-size: var(--head1);
  letter-spacing: 10px;
  color: rgb(29, 22, 101);
  font-family: var(--font-family-1);
}
.factData p {
  font-size: var(--parah);
  line-height: 2;
  text-align: left;
}

@media (max-width: 1440px) {
  .factOverlay {
    width: 80%;
    margin: 8em auto;
  }
}
@media (max-width: 768px) {
  .factData p {
    font-size: 1rem;
    line-height: 2;
  }
  .factData h1 {
    font-size: 2rem;
    line-height: 2;
    text-align: center;
  }
  .factData h2 {
    font-size: 1.5rem;
    line-height: 2;
    text-align: center;
  }
  .factData {
    padding: 1rem 2rem;
  }
  .factOverlay {
    width: 90%;
    height: 500px;
  }
}
