.academics1 {
  display: block;
  align-items: center;
  width: 80%;
  margin: 15rem auto;
}

.academicsBanner {
  width: 100%;
  height: 700px;
}

.academicsBanner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.academicLine {
  width: 100%;
  height: 2px;
  border-width: 0;
  color: rgb(77, 74, 74);
  background-color: gray;
}
.academics1 h1 {
  text-align: center;
  font-size: var(--head1);
  font-family: var(--font-family-1);
}
.academics1 h2 {
  font-family: var(--font-family-1);
  color: rgb(26, 36, 89);
  font-size: var(--head2);
  text-align: center;
}
.academics1>p {
  font-size: var(--head3);
  font-weight: bold;
  text-align: left;
}
ol {
  font-size: var(--head3);
  line-height: 2;
  color: rgb(82, 72, 59);
}
.academics1 li {
  text-align: left;
  list-style: circle;
  color: rgb(98, 90, 68);
}
@media only screen and (max-width: 1024px) {
  .academics1 h1 {
    font-size: 2rem;
  }
  .academics1 h2 {
    font-size: 1.5rem;
  }
  .academics1 p {
    font-size: 1rem;
  }
  ol {
    font-size: 1rem;
    line-height: 2;
  }
}
